import {required, decimal, minLength, maxLength, integer} from 'vuelidate/lib/validators'
export default {
  data() {
    return {
      billError: '',
      categoryError: '',
      sumError: '',
      dateError: '',
      descriptionError: ''
    }
  },
  validations: {
    candidate: {
      bill: {required},
      category: {required},
      sum: {required, decimal},
      date: {required}
    }
  },
  methods: {
    checkFormData() {
      if (!this.$v.candidate.bill.$dirty && !this.$v.candidate.bill.required) this.billError = ' '
      else this.billError = ''

      if (!this.$v.candidate.category.$dirty && !this.$v.candidate.category.required) this.categoryError = ' '
      else this.categoryError = ''

      if (!this.$v.candidate.sum.$dirty && !this.$v.candidate.sum.required) this.sumError = ' '
      if (!this.$v.candidate.sum.$dirty && !this.$v.candidate.sum.decimal) this.sumError = ' '
      else this.sumError = ''

      if (!this.$v.candidate.date.$dirty && !this.$v.candidate.date.required) this.dateError = ' '
      else this.dateError = ''

      if(this.billError || this.categoryError || this.sumError || this.dateError) return false
      return true;
    },
    resetSumError() {
      this.sumError = ''
    },
    resetDateError() {
      this.dateError = ''
    },
    resetDescriptionError() {
      this.descriptionError = ''
    },
    resetForm() {
      this.candidate.bill = ''
      this.candidate.category = ''
      this.candidate.sum = ''
      this.candidate.category = ''
    },
  }
}